.app-wrapper {
  display: grid;
  width: 1000px;
  margin: 0 auto;
  grid-template-areas: 
  "h h"
  "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* grid-gap: 10px; */
}

.header {
  grid-area: h;
  background-color: #d8c7ad;
  border-radius: 15px;
}

.header img {
  width: 50px;
}
